import { Link, useLoaderData } from "remix";
import type { LinksFunction, MetaFunction } from "remix";
import { getPosts } from "~/post";
import type { Post } from "~/post";
import stylesUrl from "~/styles/blog/index.css";

export let meta: MetaFunction = () => {
  return {
    title: "Blog - lucascoelho.dev",
    description: "Some random thoughts and stuff that I learn and share.",
  };
};

export let links: LinksFunction = () => {
  return [{ rel: "stylesheet", href: stylesUrl }];
};

export const loader = () => {
  return getPosts();
};

function PostCard ({ slug, title }) {
  return <button>{title}</button>
};

export default function Posts() {
  const posts = useLoaderData<Post[]>();
  console.log(posts);
  return (
    <div>
      <h1>Posts</h1>
      <ul>
        {[...posts].sort((a, b) => a.date > b.date ? -1 : 1).map(post => (
          <li key={post.slug}>
            <PostCard slug={post.slug} title={post.title} />
            <Link to={post.slug}>{post.title}</Link>
          </li>
        ))}
      </ul>
    </div>
  );
}
